import React from 'react'

import cx from 'classnames'

import './styles.scss'

interface ButtonPropsTypes {
  children?: React.ReactNode;
  className?: string;
}

const Button = ({
  children,
  className,
}: ButtonPropsTypes) => {
  return (
    <div className={cx('button', className)}>
      {children}
    </div>
  )
}

export default Button
