import React from 'react'

import { MotionAnimate } from 'react-motion-animate'

import './styles.scss'

interface RoadmapDescriptionsItemPropsTypes {
  image?: string;
  title?: string;
  description?: string;
  link?: string | JSX.Element;
}

const RoadmapDescriptionsItem = ({
  image, title, description,
  link = '',
}: RoadmapDescriptionsItemPropsTypes) => {
  return (
    <div className="rm-description-item">
      <MotionAnimate animation="fadeInUp">
        <img className="rm-description-item__image" src={image} alt="" />
      </MotionAnimate>
      <div className="content rm-description-item__content">
        <MotionAnimate animation="fadeInUp" delay={0.5}>
          <h3 className="rm-description-item__content-title">{title}</h3>
          <p className="content__note rm-description-item__content-note">{description}</p>
          <div className="rm-description-item__content-link">{link}</div>
        </MotionAnimate>
      </div>
    </div>
  )
}

export default RoadmapDescriptionsItem
